.button {
  display: block;
  border: none;
  font: inherit;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
}
.button--rounded {
  padding: 15px;
  border-width: 3px;
  border-style: solid;
  border-radius: 30px;
  line-height: 18px;
  background-color: transparent;
}
.button--square {
  padding: 16px 15px;
  width: 100%;
  border-radius: 2px;
  line-height: 30px;
  text-transform: uppercase;
}
.button--red {
  border-color: #d22856;
  color: #d22856;
}
.button--red:hover {
  color: #ffffff;
  background-color: #d22856;
}
.button--red:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #d22856;
}
.button--gray {
  border-color: rgba(40, 54, 69, 0.1);
  color: #dddfe0;
}
.button--gray:hover {
  border-color: rgba(40, 54, 69, 0.3);
  color: rgba(40, 54, 69, 0.3);
}
.button--gray:active {
  border-color: rgba(40, 54, 69, 0);
  color: rgba(255, 255, 255, 0.3);
  background-color: rgba(40, 54, 69, 0.3);
}
.button--green {
  color: #ffffff;
  background-color: #2aab6d;
}
.button--green:hover {
  background-color: #1c9b5e;
}
.button--green:active {
  color: rgba(255, 255, 255, 0.3);
}
